import React from "react";

const Services = () => {
  // services details
  const services = [
    {
      name: "Web Development",
      desc: "Quick learner on the latest Web Development trends. Adaptable to quickly changing technology stack.",
      icon: "fa-solid fa-code",
    },
    {
      name: "Responsive Web",
      desc: "Design with the knowledge of different screen sizes, including mobile phones and tablets.",
      icon: "fas fa-desktop",
    },
    {
      name: "App Developement",
      desc: "Develop different React apps and reusable UI components. I like optimizing and trimming off components from not really needed libraries.",
      icon: "fas fa-pencil-ruler",
    }
  ];

  return (
    <section id="services" className="section bg-light">
      <div className="container">
        {/* Heading */}
        <p className=" text-center mb-2 wow fadeInUp">
          <span className="bg-primary text-dark px-2">What I Do?</span>
        </p>
        <h2 className="text-10 fw-600 text-center mb-5 wow fadeInUp">
          How I can help your next project
        </h2>
        {/* Heading end*/}
        <div className="row gy-5 mt-5">
          {services.length > 0 &&
            services.map((service, index) => (
              <div className="col-sm-6 col-lg-4 wow fadeInUp" key={index}>
                <div className="featured-box text-center px-md-4">
                  <div className="featured-box-icon text-primary text-13">
                    {" "}
                    <i className={service.icon} />
                  </div>
                  <h3 className="text-6 fw-600 mb-3">{service.name}</h3>
                  <p className="text-muted mb-0">{service.desc} </p>
                </div>
              </div>
            ))}
        </div>
      </div>
    </section>
  );
};

export default Services;
